.training {
  margin-top: 50px;
}

.training__title {
  color: #222b45;
  text-align: center;
  font-size: 36px;
  margin-bottom: 60px;
}
.img-ellipse {
  width: 903px;
  height: 534px;
}
.training-container {
  max-width: 1440px;
  background-image: url(/public/img/ellipse.png);
  background-repeat: no-repeat;
  background-position: center;
  min-height: 850px;
}
.training-content {
  text-align: center;
  margin-bottom: 30px;
}

.frameCard {
  background-color: black;
}

.training-frame_card {
  min-height: 630px;
  display: flex;
  justify-content: space-around;
}

.training-frame__first-items {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 80px 0;
}

.training-frame__second-items {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.elliseCard{
  position: absolute;
  right: 8%;
  top: 418%;
  z-index: -12;
}
.training-frame__itemMobile{
  display: none;
}

 @media only screen and (max-width: 600px) {
  .training__title{
    display: none;
  }
  .training-container {
    
    background-image:none;
    
  }
  .training-frame__first-items{
    display: flex;
    flex-wrap: wrap;
  }
  .training-frame__itemMobile{
    display: block;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 32px;
    
  }
  .training-frame_card{
    display: none;
  }
  
}