
.footer-block-content{
   color:#fff;
   text-align: center;
   margin-top: 100px;
   
}
.footer-one__container{
   position: relative;
   margin-top: 120px;
   background-color:#2CC3D6;
   max-width: 100%;
   height: 520px ;
   z-index: -200;
  
}
.footer-one__container .item-title{
   padding-top: 72px;
   padding-bottom: 25px;
   font-size: 40px;
   font-weight: 600;
   line-height: 35px;
}
.footer-one__container .item-subtitle{
   font-size: 24px;
   font-weight: 500;
   line-height: 35px;
   padding-bottom: 25px;
}
.footer-one__container .item-description{
  width: 620px;
  height: 90px;
  margin: 0 auto;
 font-size: 24px;
 font-weight: normal;
 padding-bottom: 64px;
 letter-spacing: 0.04em;
}
.items-btn{
   padding-top: 90px;
}
.item-btn__one{
   padding-right: 16px;
}

@media screen and (max-width: 600px) {
   .footer-one__container .item-title{
      font-size: 32px;
      padding-top: 30px;
   }
   .footer-one__container .item-subtitle{
      font-size: 18px;
      margin-left: 30px;
      margin-right: 30px;     
}
.footer-one__container .item-description{
   font-size: 16px;
  
   width: 300px;
   height: 90px;
   
}
.item-btn__one{
   align-items: center;
   
}
.items-btn{
   margin-top: -40px;
}

}